.wrapper {
    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    gap: 26px;

    width: 380px;
    height: 460px;

    padding-bottom: 36px;

    background-image: url("../../images/ellipse.png"),
        url("../../images/logo.png"), url("../../images/rectangle.png"),
        url("../../images/picture.png"),
        linear-gradient(114.99deg, #471ca9 -0.99%, #5736a3 54.28%, #4b2a99 78.99%);
    background-repeat: no-repeat;
    background-position: left 150px top 178px, left 20px top 20px,
        left 0 top 214px, left 36px top 28px, top 0 left 0;

    box-shadow: -2.5777px 6.87386px 20.6216px rgba(0, 0, 0, 0.23);
    border-radius: 20px;
}

.image {
    width: 63px;
    height: 63px;
    border-radius: 50%;

    position: absolute;
    left: 163.5px;
    top: 186.5px;
}

.info {
    display: flex;
    flex-direction: column;
    gap: 16px;
    align-items: center;
}

.stats {
    margin: 0;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    text-transform: uppercase;

    color: #ebd8ff;
}

.btn {
    width: 196px;
    padding: 14px 28px;

    background-color: #ebd8ff;
    box-shadow: 0px 3.43693px 3.43693px rgba(0, 0, 0, 0.25);
    border: none;
    border-radius: 10px;

    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;

    text-transform: uppercase;
    cursor: pointer;

    color: #373737;
}

.active {
    composes: btn;
    background-color: #5cd3a8;
}